// -------------------------------------------------------------------------------------------------
// Utility Functions

// Generates a random ID string
function uid() {
    return Math.random().toString(36).substr(2,10);
}

function run(obj, args, _this) {
    if (obj instanceof Function) {
        return obj.apply(_this || null, args);
    }
    return obj;
}

// Checks if x is strictly equal to any one of the following arguments
function isOneOf(x, ...values) {
    for (let v of values) {
        if (x === v) return true;
    }
    return false;
}

// Swaps keys and values for an object
function invert(obj) {
    // SANE modified object
    var result = {};
    var keys = obj.keys();
    for (var i = 0, l = keys.length; i < l; ++i) {
        result[obj[keys[i]]] = keys[i];
    }
    return result;
}

// Merges multiple objects into a single one
function extend(first, ...others) {
    // SAME modifies object
    for (var i=0; i<others.length; ++i) {
        let obj = others[i];
        for (var key in obj) {
            if (obj.hasOwnProperty(key)) first[key] = src[key];
        }
    }
    return first;
}

function clamp(x, min = 0, max = 1) {
    return Math.max(Math.min(x, max), min);
}

function camelCase(str) {
    return str.split(/-/g).map(function (str, index) {
        return index === 0 ? str : str[0].toUpperCase() + str.substring(1);
    }).join('');
}


// -------------------------------------------------------------------------------------------------
// Function Wrappers

// Wrapper for functions to cache their result based on arguments
function cache(fn) {
    let cache = new Map();
    return function(...args) {
        let argString = args.join('--');
        if (!cache.has(argString)) cache.set(argString, fn(...args));
        return cache.get(argString);
    };
}

// Wrapper that prevents a function `fn` from being triggered more than every `t` ms.
function throttle(fn, t) {
    let delay = false;
    let repeat = false;
    return function(...args) {
        if (delay) {
            repeat = true;
        } else {
            fn.apply(null, args);
            delay = true;
            setTimeout(function() {
                if (repeat) fn(...args);
                delay = false;
                repeat = false;
            }, t);
        }
    };
}


// -------------------------------------------------------------------------------------------------
// Templates

const templateFormats = {
    number: function(x) { return x; }
    // TODO more formatting options
}

function template(template, variables = {}) {
    // TODO check if this works
    return template.replace(/\{\{\s*([a-zA-Z]+)\s*(\|\s*([a-zA-Z]+)\s*)?\}\}/g,
        function(x, val, y, format) {
            let string = variables[val] || '';
            if (format && templateFormats[format]) string = templateFormats[format](string);
            return string;
        });
}


// -------------------------------------------------------------------------------------------------
// Copying

function shallowCopy(obj) {
    // Handle (simple) strings, numbers, booleans, null and undefined
    var type = typeof obj;
    if (obj == null || isOneOf(type, 'number', 'string', 'boolean')) return obj;

    // Hande other type objects
    if (obj instanceof Number)  return new Number(obj.valueOf());
    if (obj instanceof String)  return new String(obj.valueOf());
    if (obj instanceof Boolean) return new Boolean(obj.valueOf());
    if (obj instanceof Date)    return new Date(obj.valueOf());
    if (obj instanceof RegExp)  return new RegExp(obj);

    // Handle Arrays and Objects
    return M.each(obj, function(val) { return val; });
}

let deepCopyStore = [];

function deepCopyStoreLookup(obj) {
    for (let item of deepCopyStore)
        if (item[0] === obj) return item[1];
    return null;
}

function deepCopyHelper(obj) {

    // Handle (simple) strings, numbers, booleans, null and undefined
    var type = typeof obj;
    if (obj == null || M.isOneOf(type, 'number', 'string', 'boolean')) return obj;

    // Hande other type objects
    if (obj instanceof Number)  return new Number(obj.valueOf());
    if (obj instanceof String)  return new String(obj.valueOf());
    if (obj instanceof Boolean) return new Boolean(obj.valueOf());
    if (obj instanceof Date)    return new Date(obj.valueOf());
    if (obj instanceof RegExp)  return new RegExp(obj);

    // Avoids Recursive Loops
    var x = deepCopyStoreLookup(obj);
    if (x) return x;

    var copy = obj;

    // Handle Arrays
    if (M.isArray(obj)) {
        copy = [];
        deepCopyStore.push([obj, copy]);
        for (var i = 0, l = obj.length; i < l; ++i) copy[i] = deepCopyHelper(obj[i]);

    // Handle Objects
    } else if (obj instanceof Object) {
        copy = {};
        deepCopyStore.push([obj, copy]);
        for (var attr in obj) if (M.has(obj, attr)) copy[attr] = deepCopyHelper(obj[attr]);
    }

    return copy;
}

function deepCopy(obj) {
    deepCopyStore = [];
    let copy = deepCopyHelper(obj);
    deepCopyStore = [];
    return copy;
}


