export default {
    uid, run, isOneOf, invert, extend, clamp,
    cache, throttle,
    template, 
    shallowCopy, deepCopy,
    shallowEquals, deepEquals,
    watch, unwatch,
    typeOf, isType, isString, isNumber, isInteger, isDate, isBoolean, isObject
}




